body {
  overflow: hidden;
}

.cursor {
  opacity: 0;
  background-color: #ffff00d9;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  position: absolute;
}

.cursor-fade {
  animation-name: fade;
  animation-duration: 2s;
  animation-delay: 1s;
}

@keyframes fade {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    background-color: orange;
  }
}
